import Cookie from "js-cookie";
export default {
  state: {
    isCollapse: false,
    tabsList: [
      //定义一个数据存储每次点击菜单的数据
      {
        path: "/",
        name: "home",
        label: "首页",
        icon: "home",
      },
    ],
    currentMenu: null, //用于接收当前页面的tag
    menu: [],
  },
  mutations: {
    CollapseMenu(state) {
      state.isCollapse = !state.isCollapse;
    },
    selectMenu(state, val) {
      if (val.name !== "home") {
        //如果点击的菜单不是首页
        state.currentMenu = val;
        const result = state.tabsList.findIndex(
          (item) => item.name === val.name
        ); //判断点击的菜单在current中存不存在
        if (result === -1) {
          //不存在就往tabList里面添加数据
          state.tabsList.push(val);
        }
      } else {
        //如果点击菜单是首页就重置
        state.currentMenu = null;
      }
    },
    setMenu(state, val) {
      state.menu = val;
      Cookie.set("menu", JSON.stringify(val));
    },
    clearMenu(state) {
      state.menu = [];
      Cookie.remove("menu");
    },
    addMenu(state, router) {
      if (!Cookie.get("menu")) {
        return;
      }
      const menu = JSON.parse(Cookie.get("menu"));
      state.menu = menu;
      const menuArray = [];
      menu.forEach((item) => {
        //动态传路由
        if (item.children) {
          item.children = item.children.map((item) => {
            item.component = () => import(`../views/${item.url}`);
            return item;
          });
          menuArray.push(...item.children);
        } else {
          item.component = () => import(`../views/${item.url}`);
          menuArray.push(item);
        }
      });
      //路由动态添加
      menuArray.forEach((item) => {
        router.addRoute("Main", item);
      });
    },
  },
};
