import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Main",
    component: () => import("../views/Main.vue"), //按需引入根页面
    redirect: "/home",
  },
  {
    //登录页面配置
    path: "/login",
    name: "login",
    component: () => import("../views/login/Login.vue"),
  },
];

export default new VueRouter({
  mode: "history",
  routes,
});
