//登录权限的配置
import Cookie from "js-cookie";
export default {
  state: {
    token: "",
  },
  mutations: {
    setToken(state, val) {
      state.token = val;
      Cookie.set("token", val);
    },
    clearToken(state) {
      //清空cookie
      state.token = "";
      Cookie.remove("token");
    },
    getToken(state) {
      //获取token
      state.token = state.token || Cookie.get("token"); //判断当前缓存是否有token
    },
  },
};
