import Vue from "vue";
import App from "./App.vue";
import store from "../store";
//引入mock
import "../api/mock";
//按需引入element-ui
import {
  Button,
  Radio,
  Container,
  Main,
  Aside,
  Header,
  Menu,
  MenuItem,
  MenuItemGroup,
  Submenu,
  Dropdown,
  DropdownMenu,
  DropdownItem,
  Row,
  Card,
  Col,
  Table,
  TableColumn,
  Breadcrumb,
  BreadcrumbItem,
  Form,
  FormItem,
  Input,
  Select,
  Switch,
  Option,
  DatePicker,
  Dialog,
  Pagination,
  MessageBox,
  Message,
} from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
//引路由
import router from "../router";
//axios
import axios from "axios";

Vue.config.productionTip = false;
//按需使用
Vue.use(Button);
Vue.use(Radio);
Vue.use(Container);
Vue.use(Main);
Vue.use(Aside);
Vue.use(Header);
Vue.use(Menu);
Vue.use(MenuItem);
Vue.use(MenuItemGroup);
Vue.use(Submenu);
Vue.use(Dropdown);
Vue.use(DropdownMenu);
Vue.use(DropdownItem);
Vue.use(Row);
Vue.use(Card);
Vue.use(Col);
Vue.use(Table);
Vue.use(TableColumn);
Vue.use(Breadcrumb);
Vue.use(BreadcrumbItem);
Vue.use(FormItem);
Vue.use(Form);
Vue.use(Input);
Vue.use(Select);
Vue.use(Switch);
Vue.use(Option);
Vue.use(DatePicker);
Vue.use(Dialog);
Vue.use(Pagination);

//应用axios等
Vue.prototype.$http = axios;
Vue.prototype.$confirm = MessageBox.confirm;
Vue.prototype.$message = Message;

//路由守卫
router.beforeEach((to, from, next) => {
  store.commit("getToken"); //防止页面刷新丢失token
  const token = store.state.user.token;
  if (!token && to.name !== "login") {
    //判断是否登录页面，否则跳转到login页面
    next({ name: "login" });
  } else {
    next();
  }
});

new Vue({
  store,
  router,
  render: (h) => h(App),
  created() {
    store.commit("addMenu", router);
  },
}).$mount("#app");
